/* src/App.css */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  height: 100vh;
  width: 100vw;
}

.main-container {
  width: 100%;
  height: 90%;
}

.color-box {
  height: 90%;
  display: flex;
}

.color-display {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  position: relative;
  cursor: pointer;
}

.hover-display {
  position: absolute;
  font-size: 24px;
  top: 30%;
}

.color-text{
  position: absolute;
  top: 70%;
  text-transform: uppercase;
  font-size: 24px;
}

@media (max-width : 500px) {
  .color-box {
    flex-direction: column;
  }

  .color-display {
    height: 70px;
  }

  .hover-display {
    left: 70%;
    font-size: 16px;
  }
  
  .color-text{
    top: 30%;
    left: 20%;
    font-size: 20px;
  }

}